import logo from './logo.svg';
import './App.css';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import Home from './pages/Home';
import TeamPage from './pages/TeamPage';

import Navbar from './components/Navbar';
import PolicyPage from './pages/PrivacyPolicy';
import Corporate from './pages/CorporateBookings';
import Nri from './pages/Nri';
import ONDC from './pages/ONDC';
import About from './pages/About';
import MobileAdd from './components/mobileadd';
import Contact from './pages/Contact';
import Blog from './pages/Blog';
import React, { useState, useEffect } from 'react';
import Footer from './components/Footer';


function App() {

  
  const [showButton, setShowButton] = useState(false);
  
  useEffect(() => {
    function handleScroll() {
      if (window.pageYOffset > 300) {
        setShowButton(true);
      } else {
        setShowButton(false);
      }
    }

    window.addEventListener('scroll', handleScroll);

    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  function scrollToTop(e) {
    e.preventDefault();
    window.scrollTo({ top: 0, behavior: 'smooth' });
  }

  return (
    
   

    <BrowserRouter>
   
   {showButton && (
        <button id="button" className="show" onClick={scrollToTop}>
        
        </button>
      )}

        <Navbar/>

      <Routes>
        <Route path='/' element={<Home/>}/>
        <Route path='/team' element={<TeamPage/>}/>
        <Route path='/privacypolicy' element={<PolicyPage/>}/>
        <Route path='/Corporate' element={<Corporate/>}/>
        <Route path='/Nri' element={<Nri/>}/>
        <Route path='/ONDC' element={<ONDC/>}/>
        <Route path='/About' element={<About/>}/>
        <Route path='/Contact' element={<Contact/>}/>
        <Route path='/Blog' element={<Blog/>}/>
        
      </Routes>
      <MobileAdd />
      <Footer />
    </BrowserRouter>
  );
}

export default App;
